tion: column;\n    align-items: center;\n    justify-content: center;\n}","@import \"./variables.scss\";\n\n#setup{\n    // background-color: red;\n    @include flex-center;\n    flex-wrap: wrap;\n    //desktop width/height\n    // width: 30%;\n    // min-height: 30%;\n    width: $set-up-size;\n    height: $set-up-size;\n    background-color: $blue-2;\n    padding: 2rem;\n    gap:1rem;\n    border-radius: 20px;\n    border:  4px solid $blue-4;\n    .ship{\n        background-color: $green-1;\n        // border: 1px solid black;\n\n        *{\n            //all shipcells none draggable\n            pointer-events: none;\n        }\n    }\n    .ship:hover{\n        background-color: $green-2;\n        transform: scale(1.03);\n        transition: 200ms ease-in-out\n    }\n    .shipblock{\n        @include cell;\n        border: 1px solid black; \n    }\n    .isX{\n        //horizontal?\n        display:flex;\n        transition: 300ms ease-in-out\n    }\n}\n\n.mybutton{\n    display: inline-block;\n    font-size: 2rem;\n    padding: 0.5rem 1.2rem;\n    border-radius: 1.5rem;\n    color: #f5f4f3;\n    background-color: $green-2;\n    border: 0.2rem solid $green-1;\n\n    &:hover{\n        background-color: $green-1;\n        border: 0.2rem solid $green-2;\n        color: $green-2;\n        // transform: scale(1.1);\n        transition: 200ms ease-in-out;\n    }\n    \n\n}\n\n\n#play-again{\n    @include flex-column-center;\n    font-size: 1rem;\n    position: absolute;\n    border-radius: 30px;\n    background-color: $blue-2;\n    border: 5px solid $blue-4;\n    min-width: 400px;\n    min-height: 250px;\n    z-index:10;\n}\n\n.hide{\n    display:none !important;\n}\n\n.overlay-active{\n    position: absolute;\n    top:0;\n    left:0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(33, 33, 33, 0.674);\n}\n\n[hidden]{\n    display:none !important;\n}\n\n\n","\n@import './mixins.scss' ;\n@import './variables.scss';\n@import './board.scss';\n@import \"./pregame.scss\";\n\n\n:root{\n    font-size: 18px;\n    font-family: \"Ubuntu\",sans-serif;\n}\n\n*,\n*::before,\n*::after{\n    margin:0;\n    padding:0;\n    box-sizing: border-box;\n}\nimg{\n    height:100%;\n    width: 100%;\n}\n\nbody{\n    background-color: #2f9fe6;\n    // background-image: url(\"../assets/images/wallpaper3.jpeg\");\n    background-size: cover;\n    // background-size: 20%;\n}\n\nheader{\n    font-family: \"Alumni Sans Collegiate One\", sans-serif;\n    font-size: 5rem;\n    @include flex-center;\n    margin-bottom: 2rem;\n    background-color: $blue-2;\n    color: $blue-4;\n}\n\n\nmain{\n    @include flex-column-center\n}\n\n.content{\n    @include flex-center;\n    margin-bottom: 2rem; \n    gap: 2rem;\n    padding: 2rem;\n}\n\n"],"sourceRoot":""}]);
